/*css reset*/
*{box-sizing: border-box;}
*:before,*:after{box-sizing: border-box;}
*{margin: 0;padding: 0;}
ul,ol{list-style:none;}
a{color: inherit;text-decoration: none;}
img{max-width: 100%;max-height: 100%;}
/* style */
body{background-color: #eee;
width: 100%;
height: 100%;
background-image: url(imgs/bg.jfif);
background-repeat: no-repeat;
background-size: cover;
-webkit-background-size: cover;
-o-background-size: cover;
background-position: center 0;
border: none;
}
@media (max-width:500px) {
    body{background-image: url(imgs/背景.jpg);
        background-repeat: no-repeat;
        background-size: cover;
        -webkit-background-size: cover;
        -o-background-size: cover;
        background-position: center 0;}
    
}

.globalHeader{
    margin: 20px;
    
}
@media (min-width:500px) {
    .globalHeader{
        margin: 60px 0 80px;
    }
    
}
.searchForm{
    display: flex;
    justify-content: space-between;
    
}

@media (min-width:500px) {
    .searchForm{
        margin-left: auto;
        margin-right: auto;
        max-width:600px;
}}

.searchForm>input{
    width: 100%;
    margin-right: 10px;
    height: 40px;
    padding: 0 10px;
    border: 1px solid #ddd;
    border-radius: 20px;
    background: #ffffff00;
}
@media (max-width:500px) {
    .searchForm>input{
        width: 100%;
        margin-right: 10px;
        height: 40px;
        padding: 0 10px;
        border: 1px solid #a3a3a8;
        border-radius: 20px;
        background: #ffffff00;
    }
}
.searchForm>button{
    white-space: nowrap;
    padding: 0 28px;
    border: none;
    border-radius: 4px;
    background: #0282b9;
    color: white;
    font-size: 16px;
    border-radius: 20px;

}

.globalMain{
    max-width: 900px;
    margin-left: auto;
    margin-right: auto;
}

.siteList{
    display: flex;
    flex-wrap: wrap;
    margin: 20px;
    justify-content: space-between;
}
@media (min-width:500px) {
    .siteList{
        margin-left: 0;
        margin-right: -25px;
        justify-content: flex-start;
    }
    
}

.siteList>li{
    margin-bottom: 10px;
}
@media (min-width:500px) {
    .siteList>li{
        margin-right: 25px;
}
}
.siteList .site{
    
    width: 160px;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    background: white;
    border: 1px solid #eee;
    border-radius: 4px;
    padding: 20px 0;
    position: relative;
    cursor: pointer;
    background: #ffffff00;
    color: #eee;
    
    
}

.siteList .site>.logo{
    width: 64px;
    height: 64px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 50px;
}

.siteList .site>.link{
    font-size: 14px;
    margin-top: 4px;
}
@media (max-width:500px) {
    .siteList .site{
    
        width: 160px;
        display: flex;
        justify-content: center;
        flex-direction: column;
        align-items: center;
        border: 1px solid #000;
        border-radius: 4px;
        padding: 20px 0;
        position: relative;
        cursor: pointer;
        background: #ffffff00;
        color: #000;
        
        
    }
    
    
}
.siteList .addButton{
    border: 1px solid #ddd;
    width: 160px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 20px 0;
    background: #ffffff00;
    border-radius: 4px;
    color: #eee;
     
}

@media (max-width:500px) {
    .siteList .addButton{
        border: 1px solid #000;
        width: 160px;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        padding: 20px 0;
        background: #ffffff00;
        border-radius: 4px;
        color: #000;
        
         
    }
    
}
.siteList .site > .close{
    position: absolute;
    top: 0;
    right: 5px;
    display: none;
    cursor: default;
   
}

.siteList .site:hover > .close {
    display: block;
  }

.siteList .addButton .icon {
    width: 56px;
    height: 56px;
    vertical-align: -0.15em;
    fill: currentColor;
    overflow: hidden;
}

.siteList .addButton .text{
    font-size: 14px;
    margin-top: 4px;
}

.siteList .addButton .icon-wrapper{
    width: 64px;
    height: 64px;
    display: flex;
    justify-content: center;
    align-items: center;
}



